.button {
    height: 50px;
    border-radius: 8px;
    font-weight: 500;
    background-color: #fc036f;
    color: #fff;
  
    padding: 0 32px;
  
    display: flex;
    justify-content: center;
    align-items: center;
  
    cursor: pointer;
    border: 0;
  
    transition: filter 0.2s;
  
    img {
      margin-right: 8px;
    }
  
    &.outlined {
      background: #fff;
      border: 1px solid #fc036f;
      color: #fc036f;
  
      &.dark {
        background: #333;
      }
    }
  
    @media only screen and (max-width: 600px) {
      &.outlined {
        width: 35px;
      }
    }
  
    &:not(:disabled):hover {
      filter: brightness(0.9);
    }
  
    &:disabled {
      opacity: 0.6;
      cursor: not-allowed;
    }
  }